.newsSearchWrap{
	position:relative;
	border:1px solid #000;
}
.newsSearch{
	padding:15px 0px 15px 75px;
	border:none;
	border-radius:20px;
	box-shadow:0px 0px 10px rgba(0, 0, 0, 0.3);
}


.news{
	width:330px;
	height:100px;
	display:flex;
	flex-direction:column;
}
.news_grid{
	display:grid;
	grid-template-columns:repeat(2, 1fr);
	grid-gap:25px;
}
.news_item{
	width:150px;
	height:200px;
	box-shadow:0px 0px 10px rgba(0, 0, 0, 0.3);
	border-radius:15px;
}