.Time_table{
	width:330px;
	height:200px;
	box-shadow:0px 3px 5px rgba(0, 0, 0, .7);
	border-radius:20px;
}
.row{
	display:flex;
	justify-content:space-between;
	align-items:center;
	padding:0px 20px 0px 20px;
}
.row p{
	font-size:18px;
}
.row p span{
	margin-right:10px;
	font-size:32px;
	font-weight:bold;
}
.today{
	background:rgb(9, 48, 74);
	color:#fff;
	padding:10px;
	border-radius:10px;
}
.time_line{
	display:flex;
	justify-content:space-around;
	align-items:center;
	padding:10px 0px 10px 0px;
	font-size:18px;
	border-bottom:1px solid #000;
}
.active{
	background:rgba(255, 118, 72, 1);
	padding:10px;
	border-radius:10px;
}