*{
    box-sizing:border-box;
}
.App{
    width:100vw;
    height:100vh;
    display:flex;
    align-items:center;
    position:fixed;
    flex-direction:column;
}
.header{
    width:330px;
    height:50px;
    margin:15px;
    display:inherit;
    justify-content:space-between;
    align-items:center;
}
.App-title{
    font-size:18px;
}
.sidebar-btn{
    background:none;
    background-size:cover;
    width:30px;
    height:20px;
    border:none;
}
.navbar{
    width:330px;
    padding:10px 20px 10px 20px;
    display:flex;
    justify-content:space-around;
    align-items:center;
    position:fixed;
    left:calc(50vw - 165px);
    bottom:20px;
    border-radius:20px;
    box-shadow:0px 5px 15px rgba(0, 0, 0, 0.3);
}
.page-link{
    background-size:cover;
    width:25px;
    height:25px;
    border:none;
}
.sidebar{
    background:rgba(9, 48, 74, 0.9);
    width:200px;
    height:100vh;
    display:flex;
    justify-content:center;
    align-items:center;
    position:fixed;
    top:0px;
    left:-200px;
    transition-duration:0.45s;
    transition-timing-function:ease;
}
.link{
    display:inherit;
    flex-direction:column;
}
.link a{
    color:#fff;
    font-size:24px;
    margin-bottom:50px;
    text-decoration:none;
}
.link a:nth-child(5){
    color:red;
}